import React from "react";
import { Link } from "gatsby"

export default class Form extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      <form
        onSubmit={this.submitForm}
        action="https://formspree.io/mnbblykm"
        method="POST"
        className="row topBefore padding5_top"
      >
        
        <div className="small-7 medium-4 columns textfield_submit">
            test added post first and _replyto2
            <input id="email" className="unsent" type="email" name="_replyto" placeholder="Leave your e-mail..." required/>
        </div>
        <div className="small-5 medium-3 columns btn_submit">
            <input id='send-button' className="submitBtn" type="submit" value="Subscribe"/>
        </div>
        <div className="small-12 columns no-spam">
            <small>No spam, no fuss. Unsubscribe anytime. <Link to="/legal/">Privacy policy</Link></small>
        </div>
        {status === "SUCCESS" ? <p>Thanks!</p> : <button>Submit</button>}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
        
      </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}




